import React, {useState} from 'react'
import { TextField, Button } from '@mui/material'
import AuthComponent from './AuthComponent';
import { useDispatch, useSelector } from 'react-redux';
import { setAuthVisible, setAuthorized } from '../../store/tasks';

const styles = {
    form: {
        color: 'black',
        fontSize: "20px",
        position: "absolute",
        left: "50%",
        top: "50%",
        width: "300px",
        height: "240px",
        display: "flex",
        flexDirection: "column",
        alignItems: "center",
        backgroundColor: "transparent",
        borderRadius: "10px",
        zIndex: 4,
        transform: "translate(-50%, -50%)",
        justifyContent: "center",
        textAlign: 'center',
    },
    field: {
        backgroundColor: "#fff",
        width: "300px",
        height: "40px",
        margin: "12px",
        borderRadius: 5,
    },
    buttons: {
        display: 'flex',
        flexDirection: 'row',
        width: "360px",
        justifyContent: 'space-between',
        marginTop: '6px'
    }
}

const SigninComponent = () => {
    const [name, setName] = useState("")
    const [number, setNumber] = useState("")
    const [password, setPassword] = useState("")
    const visible = useSelector((state) => state.data.authVisible);
    const dispatch = useDispatch();
    
    return (
        <div>
            <div style={styles.form} className='auth'>
            <TextField 
                style={styles.field} 
                label="Номер Телефона" 
                variant="outlined" 
                InputLabelProps={{
                    style: {
                      height: 40,
                      lineHeight: "14px",
                    },
                  }}
                inputProps={{ 
                    style: { 
                        height: 40,
                        padding: '0 20px',
                    } 
                }}
                onChange={(txt) => { setPassword(txt.target.value) }}
            />
            <TextField 
                style={styles.field} 
                label="Пароль" 
                variant="outlined" 
                onChange={(txt) => { setPassword(txt.target.value) }}
                InputLabelProps={{
                    style: {
                      height: 40,
                      lineHeight: "14px",
                    },
                  }}
                inputProps={{ 
                    style: { 
                        height: 40,
                        padding: '0 20px',
                    } 
                }}
            />
            <Button 
                variant="contained"
                onClick={() => {
                    dispatch(setAuthorized(true))
                    console.log(visible)
                }}
            >
                ВОЙТИ
            </Button>

            <p>Нету аккаунта? </p>
            <a href="#"                 
                onClick={() => {
                    dispatch(setAuthVisible(!visible))
                    console.log(visible)
                }}
            >
                Регистрируйтесь!
            </a>
            
            
        </div>
        <AuthComponent visible={visible}/>
        </div>
        
    )
}

export default SigninComponent;